<template>
  <div class="login-container">
    <el-form 
      ref="loginForm" 
      :model="loginForm" 
      :rules="loginRules" 
      auto-complete="on" 
      label-position="left" 
      label-width="0px"
      class="card-box login-form">
      <h3 class="title">登录</h3>

      <el-form-item prop="mail">
        <span class="svg-container svg-container_login">
          <svg-icon icon-class="email" />
        </span>
        <el-input 
          v-model="loginForm.mail" 
          name="mail" 
          type="text" 
          auto-complete="on" 
          placeholder="登录邮箱" />
      </el-form-item>

      <el-form-item prop="password">
        <span class="svg-container">
          <svg-icon icon-class="password"/>
        </span>
        <el-input 
          :type="pwdType" 
          v-model="loginForm.password" 
          name="password" 
          auto-complete="on" 
          placeholder="登录密码"
          @keyup.enter.native="handleLogin"/>
        <span 
          class="show-pwd" 
          @click="showPwd"><svg-icon icon-class="eye" /></span>
      </el-form-item>

      <el-form-item>
        <el-button 
          :loading="loading" 
          type="primary" 
          style="width:100%;" 
          @click.native.prevent="handleLogin">
          登录
        </el-button>
      </el-form-item>

      <!--<div class='tips'>-->
      <!--<span style="margin-right:20px;">username: admin</span>-->
      <!--</span> password: admin</span>-->
      <!--</div>-->
    </el-form>
  </div>
</template>

<script>
import { isvalidUsername } from '@/utils/validate'

const md5 = require('js-md5')

export default {
  name: 'Login',
  data() {
    const validateUsername = (rule, value, callback) => {
      if (!isvalidUsername(value)) {
        callback(new Error('请输入正确的用户名'))
      } else {
        callback()
      }
    }
    //    const validatePass = (rule, value, callback) => {
    //      if (value.length < 1) {
    //        callback(new Error('密码不能小于5位'))
    //      } else {
    //        callback()
    //      }
    //    }
    return {
      loginForm: {
        mail: '',
        password: ''
      },
      loginRules: {
        mail: [{ required: true, trigger: 'blur', validator: validateUsername }],
        password: [{ required: true, trigger: 'blur' }]
      },
      pwdType: 'password',
      loading: false
    }
  },
  methods: {
    showPwd() {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },
    handleLogin() {
      console.log('登录')

      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true
          let md5Password = md5(this.loginForm.password)
          console.log(md5Password)
          this.$store.dispatch('Login',{
            mail: this.loginForm.mail,
            password: md5Password
          }).then(() => {
            this.loading = false
            console.log('登录')
            this.$router.push({ path: '/auditManagement' })
          }).catch(() => {
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss">
  @import "src/styles/mixin.scss";
  $bg:#2d3a4b;
  $dark_gray:#889aa4;
  $light_gray:#eee;

  .login-container {
    @include relative;
    height: 100vh;
    background-color: $bg;
    input:-webkit-autofill {
      -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
      -webkit-text-fill-color: #fff !important;
    }
    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
    }
    .el-input {
      display: inline-block;
      height: 47px;
      width: 85%;
    }
    .tips {
      font-size: 14px;
      color: #fff;
      margin-bottom: 10px;
    }
    .svg-container {
      padding: 6px 5px 6px 15px;
      color: $dark_gray;
      vertical-align: middle;
      width: 30px;
      display: inline-block;
      &_login {
        font-size: 20px;
      }
    }
    .title {
      font-size: 26px;
      font-weight: 400;
      color: $light_gray;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
    .login-form {
      position: absolute;
      left: 0;
      right: 0;
      width: 400px;
      padding: 35px 35px 15px 35px;
      margin: 120px auto;
    }
    .el-form-item {
      border: 1px solid rgba(255, 255, 255, 0.1);
      background: rgba(0, 0, 0, 0.1);
      border-radius: 5px;
      color: #454545;
    }
    .show-pwd {
      position: absolute;
      right: 10px;
      top: 7px;
      font-size: 16px;
      color: $dark_gray;
      cursor: pointer;
      user-select:none;
    }
    .thirdparty-button{
      position: absolute;
      right: 35px;
      bottom: 28px;
    }
  }
</style>



